

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import tools from '../../lib/tools.js';
export default {
name:'fanilySignUpIndex',
components:{Tab, TabItem, Sticky,Cell,Group,XInput,XTextarea,Actionsheet,Toast,PopupPicker,CellBox,XDialog,Checker, CheckerItem,Datetime,Selector },
directives: {TransferDom},
data(){
return {
managerId:'',
doctorId:'',
btnTitle:'',
tabItems:['','',''],
selectTag:'',
isAddOrUpdate:true,// true add false update
showMenu:false,
showDoubleMenu:false,
showToast:false,
toastMsg:'',
showDiseases:false,//
showCheck:false,//
showRadio:false,//
tempDiseases:[],//datalist     
tempCheckArr:[],//dataList 
tempRadioText:'',//datalist 
tempKey:'',//key
menuObj: {},//
dictionary:{},

















showMask:false,
otherText:'',       // 
diseaseOptions:[], // option
templateFiled:'',// datalist
geneticHistory:'', // 
tempCheckerItems:[],//
tempRadioItems:[],//
fieldObj:{},
diseaseArr:['','','','','','','','','','','','','',''],//
disabilityArr:['','','','','','','','',''], // 
diseaseText:'',// 
surgeryText:'',// 
traumaText:'',// 
bloodTransfusionText:'',// 
fatherText:'',// 
motherText:'',// 
brotherText:'',// 
childrenText:'',// 
disabilityText:'',// 
historyOfDrugAllergyText:'',//  
exposureHistoryText:'', // 
geneticHistoryText:'', //  
kitchenExhaustText:'', //  
fuelTypeText:'', //  
drinkingWaterText:'', //  
lavatoryText:'', //  
animalHusbandryAndBarText:'', //  
info:{// 
name:'',// 
sex:'',// 

cardId:'',// 
birthday:'',// 
contactPeople:'',// 
contactMobile:'',// 
residentType:'',// 

permanentAddress:'',// 
nation:'',// 

maritalStatus:'',// 

culturalStatus:'',// 

occupation:'',// 

workUnit:'',// 
presentAddress:'',// 
medicalType:'',// 

bloody:'',// 

rh:'',// RH





dataList:[] // 
},
reg:{
IDCard:/(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
phone:/^[1][3,4,5,7,8][0-9]{9}$/,
},
showOtherText:''
}
},
mounted(){
let vm = this;
this.toggleTab('');
this.managerId = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); //managerId == userid
console.log(this.managerId);
this.doctorId = tools.getRequestParam('doctorId');
if(!this.managerId || this.managerId=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}

this.$http.post('/apiUser/queryAccount?managerId='+this.managerId,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info.name = data.data.name;
vm.info.sex = data.data.sex;
}else{
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
}
}else{
vm.showToast = true;
vm.toastMsg = '';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
});

this.initDictionary();
},
methods:{
initDictionary(){
var vm = this;
console.log('initDictionary');

this.$http.get('/api/contracte/queryDictionary',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
let tempData = data.data;
let tempObj = {};
let tempFieldObj = {};
for(let i=0;i<tempData.length;i++){
let itemType = tempData[i].itemType;
tempObj[itemType] = [tempData[i]];
tempFieldObj[itemType] = tempData[i].desc;
for(let j=0;j<tempData.length;j++){
if(tempData[j].itemType==itemType){
tempObj[itemType] = Array.from(new Set([...tempObj[itemType],tempData[j]]));
}
}
}
this.dictionary = Object.assign(tempObj);
this.fieldObj = tempFieldObj;
for(let obj of tempObj['3']){
this.diseaseOptions.push({key:obj.itemId,value:obj.itemName});
}

}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
})
},
toggleTab(item){// tab
this.selectTag = item;
let vm = this;
},
clickUpdate(field,key){//   
this.tempKey = key;
this.tempField = field;
let temp = this.dictionary[key];
let tempObj = {};
for(let i=0;i<temp.length;i++){
tempObj[temp[i].itemId] = temp[i].itemName;
}
this.menuObj = tempObj;
this.showMenu = true;
},
editMenu(key){//   
if(key!='cancel'){


this.info[this.tempField] = key+'';
this.showMenu = false;
}
},
clickDoubleUpdate(field,key,type,templateFiled){ //   datalist 

this.tempKey = key; // 
this.tempField = field;  // 
this.tempFieldType = (type || key); // 
this.templateFiled = templateFiled; // this
let temp = this.dictionary[type || key];




let tempArr = {};
for(let i=0;i<temp.length;i++){
tempArr[i] = {itemId:temp[i].itemId,itemName:temp[i].itemName};
}

if([4,25,26,27,28].indexOf(key^0)>=0){
this.tempCheckerItems = tempArr;
this.showCheck = true;
}else if([18,19,24,5,6,7,8,9,21,22,23].indexOf(key^0)>=0){
this.tempRadioItems = tempArr;
this.showRadio = true;
}else if([3].indexOf(key^0)>=0){
this.showDiseases = true;
}
this.showDoubleMenu = true;
},
editDoubleMenu(){ //  datalist 
let vm = this;



if(['3'].indexOf(this.tempKey)>=0){
let tempText = [];
if(this.info[this.tempField].length==0){
this[this.templateFiled].forEach((obj) => {

this.dictionary[3].forEach((item) => {
if(item.itemId == obj.itemId){

obj.itemName = obj.itemName || item.itemName;
tempText.push(obj.itemName +':'+ obj.time);
}
});
this.info[this.tempField].push(obj);
});
}else{
this.info[this.tempField] = this.info[this.tempField].filter((item) => {
return item.itemTypeId != this.tempKey;
});
this[this.templateFiled].forEach((obj) => {

this.dictionary[3].forEach((item) => {
if(item.itemId == obj.itemId){

obj.itemName = obj.itemName || item.itemName;
tempText.push(obj.itemName +':'+ obj.time);
}
});
this.info[this.tempField].push(obj);
});
}
if(this.tempKey == '3'){ //  +
this.diseaseText = tempText.join(';');
}
}else if(['25','26','27','28','4'].indexOf(this.tempKey)>=0){ //  
let tempText = [];
if(this.info[this.tempField].length==0){
this[this.templateFiled].forEach((key) => {

this.dictionary[this.tempFieldType].forEach((item) => {

if(item.itemId == key){
let obj = {
itemId:key,
itemName:item.itemName,
itemTypeId:this.tempKey,
}
if(['25','26','27','28'].indexOf(this.tempKey)>=0){
obj.itemName = (item.itemId == 11 || item.itemId == 12)?this.otherText:item.itemName;
}
if(this.tempKey==4){
obj.itemName = (item.itemId == 1)?this.otherText:item.itemName;
}
this.info[this.tempField].push(obj);
tempText.push(obj.itemName);
}
});
});
}else{


this.info[this.tempField] = this.info[this.tempField].filter((item) => {
return item.itemTypeId != this.tempKey;
});


this[this.templateFiled].forEach((key) => {
this.dictionary[this.tempFieldType].forEach((sitem) => {

if(sitem.itemId == key){
let obj = {
itemId:key,
itemName:sitem.itemName,
itemTypeId:this.tempKey,
}

if(['25','26','27','28'].indexOf(this.tempKey)>=0){
obj.itemName = (sitem.itemId == 11 || sitem.itemId == 12)?this.otherText:sitem.itemName;
}
if(this.tempKey==4){
obj.itemName = (sitem.itemId == 1)?this.otherText:sitem.itemName;
}
this.info[this.tempField].push(obj);
tempText.push(obj.itemName);
}
});
});

}
if(this.tempKey == '25'){//  
this.fatherText = tempText.join(';');
}else if(this.tempKey == '26'){ //  
this.motherText = tempText.join(';');
}else if(this.tempKey == '27'){ //  
this.brotherText = tempText.join(';');
}else if(this.tempKey == '28'){ //  
this.childrenText = tempText.join(';');
}else if(this.tempKey == '4'){ //  
this.disabilityText = tempText.join(';');
}
console.log('---',tempText);
}else if(['24','5','6','7','8','9','18','19','21','22','23'].indexOf(this.tempKey)>=0){ //  
let tempText = [];
if(this.info[this.tempField].length==0){
let tempObj = {
itemId:this[this.templateFiled],
itemName:this.fieldObj[this.tempKey],
itemTypeId:this.tempFieldType,
}
this.dictionary[this.tempKey].some((item) => {
if(item.itemId == this[this.templateFiled]){
tempObj.itemName = this.otherText || item.itemName;
tempText.push(tempObj.itemName);
}
});

this.info[this.tempField].push(tempObj);
}else{
this.info[this.tempField] = this.info[this.tempField].filter((item) => {
return item.itemTypeId != this.tempKey;
});

let tempObj = {
itemId:this[this.templateFiled],
itemName:this.fieldObj[this.tempKey],
itemTypeId:this.tempFieldType,
}
this.dictionary[this.tempKey].some((item) => {
if(item.itemId == this[this.templateFiled]){
tempObj.itemName = this.otherText || item.itemName;
tempText.push(tempObj.itemName);
}
});

this.info[this.tempField].push(tempObj);










































}
if(this.tempKey == '24'){ //  
this.geneticHistoryText = tempText.join(';');
}else if(this.tempKey == '5'){ //  
this.kitchenExhaustText = tempText.join(';');
}else if(this.tempKey == '6'){ //  
this.fuelTypeText = tempText.join(';');
}else if(this.tempKey == '7'){ //  
this.drinkingWaterText = tempText.join(';');
}else if(this.tempKey == '8'){ //  
this.lavatoryText = tempText.join(';');
}else if(this.tempKey == '9'){ //  
this.animalHusbandryAndBarText = tempText.join(';');
}else if(this.tempKey == '18'){ //  
this.historyOfDrugAllergyText = tempText.join(';');
}else if(this.tempKey == '19'){ //  
this.exposureHistoryText = tempText.join(';');
}else if(this.tempKey == '21'){ //  
this.surgeryText = tempText.join(';');
}else if(this.tempKey == '22'){ //  
this.traumaText = tempText.join(';');
}else if(this.tempKey == '23'){ //  
this.bloodTransfusionText = tempText.join(';');
}
console.log('---',tempText);
}

this.tempCheckerItems = [];
this.tempRadioItems = [];
this.tempCheckArr = [];
this.tempDiseases = [];
this.tempRadioText = '';
this.otherText = '';
this.showDoubleMenu = false;
this.showCheck = false;
this.showRadio = false;
this.showDiseases = false;
},
addDisease(){ // 




console.log(this.fieldObj[this.key]);
this.tempDiseases.push({
itemTypeId:3,
time:'',
itemId:'1',
itemName:''
});
},
next(){
let uri = '';
if(this.selectTag==''){
uri = '';
}else if(this.selectTag==''){
uri = '';
}
this.toggleTab(uri);
document.getElementsByTagName('body')[0].scrollTop = 0;
},
saveInfo(e){ // 
let vm = this;
console.log(JSON.stringify(this.info));
let tempObj = this.info;

for(let key in tempObj){
console.log(key,tempObj[key]);
if(tempObj[key] || tempObj[key]==0){
if(tools.isObj(tempObj[key])){
for(let skey in tempObj[key]){
if(!(tempObj[key][skey]+'')){
this.showToast = true;
this.toastMsg = '';
return false;
}
}
}else if(tools.isArray(tempObj[key])){
for(let i=0;i<tempObj[key].length;i++){
if(tools.isObj(tempObj[key][i])){
for(let sskey in tempObj[key][i]){
if(sskey == 'time' && tempObj[key][i]['itemTypeId']==3 && tempObj[key][i][sskey]==''){
tempObj[key][i][sskey] = tools.simpleDateFormat(new Date(),'yyyy-MM-dd');
}
if(!(tempObj[key][i][sskey]+'')){
console.log(tempObj[key][i][sskey]+'')
this.showToast = true;
this.toastMsg = '';
return false;
}
}
}
}
}else{
console.log('string-',tempObj[key]);
if(!(tempObj[key]+'') && tempObj[key]!=0 ){
this.showToast = true;
this.toastMsg = '';
return false;
}
}
}else{
this.showToast = true;
this.toastMsg = '';
return false;
}
}

tempObj.managerId = this.managerId;
tempObj.doctorId = this.doctorId;



let regDataList = tempObj.dataList;
let fieldList = Object.assign({},this.fieldObj);
for(let key in regDataList){
let item = regDataList[key];
for(let field in fieldList){
if(item.itemTypeId == field || field>29){
delete fieldList[field^0];
}
}
}

console.log(Object.keys(fieldList).length,fieldList);

if(Object.keys(fieldList).length>10){
this.showToast = true;
this.toastMsg = '.....';
return false;
}
this.showMask = true;





this.$http.axios({method:'POST',url:'/api/contracte/addUserArchives',data:tempObj}).then((result) => {
result = result.data;
if(result.resultCode && result.resultCode.code=='SUCCESS'){
vm.showMask = false;
vm.showToast = true;
vm.toastMsg = '';
tools.next('/m/myFamilyDoctor.html');
}else{
vm.showMask = false;
console.error(result);
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
}
},(error) => {
vm.showMask = false;
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
});

},
close(){ // 
this.showDoubleMenu = false;
this.showCheck = false;
this.showRadio = false;
this.showDiseases = false;
this.tempCheckArr = [];
this.tempRadioText = '';
},
tegExpCardID(){

if(this.info.cardId){
if(this.reg.IDCard.test(this.info.cardId)){
this.info.birthday = tools.simpleDateFormat(this.getBirthdayFromIdCard(this.info.cardId),'yyyy-MM-dd');

}else{
this.showToast = true;
this.toastMsg = '';
}
}
},
getBirthdayFromIdCard(idCard){
var birthday = "";
if(idCard != null && idCard != ""){
if(idCard.length == 15){
birthday = "19"+idCard.substr(6,6);
} else if(idCard.length == 18){
birthday = idCard.substr(6,8);
}

birthday = birthday.replace(/(.{4})(.{2})/,"$1-$2-");
}
return birthday;
},
regPhone(){
if(this.info.contactMobile){
if(!this.reg.phone.test(this.info.contactMobile)){
this.showToast = true;
this.toastMsg = '';
return false;
}
}
}
},
watch:{
tempCheckArr:{
handler(curVal,oldVal){

if(this.tempFieldType==3){
if(Array.prototype.slice.call(curVal).length>1 && Array.prototype.slice.call(curVal).filter(item => item=='0').length>0){
this.tempCheckArr = curVal[Array.prototype.slice.call(curVal).length-1]=='0'?Array.prototype.slice.call(curVal).filter(item => item=='0'):Array.prototype.slice.call(curVal).filter(item => item!='0');
}
this.showOtherText = Array.prototype.slice.call(curVal).filter(item => (item=='11' || item=='12')).length>0?true:false;
}else if(this.tempFieldType==4){
if(Array.prototype.slice.call(curVal).length>1 && Array.prototype.slice.call(curVal).filter(item => item=='0').length>0){
this.tempCheckArr = curVal[Array.prototype.slice.call(curVal).length-1]=='0'?Array.prototype.slice.call(curVal).filter(item => item=='0'):Array.prototype.slice.call(curVal).filter(item => item!='0');
}
this.showOtherText = Array.prototype.slice.call(curVal).filter(item => item=='1').length>0?true:false;
}
},
deep:true
},
tempRadioText(curVal,oldVal){

if(this.tempFieldType==18){
if(curVal==4){
this.showOtherText = true;
}else{
this.showOtherText = false;

}
}
}
}
}



